import numeral from 'numeral'

numeral.register('locale', 'br', {
  delimiters: {
    thousands: '.',
    decimal: ','
  },
  abbreviations: {
    thousand: 'mil',
    million: 'milhes',
    billion: 'bilhes',
    trillion: 'trilhes'
  },
  ordinal: function () {
    return ''
  },
  currency: {
    symbol: 'R$'
  }
})

export default function (value, locale) {
  numeral.locale(locale)

  if (value === undefined || value === null) return ''

  return numeral(value).format('0,0.00')
}
